import "./assets/scss/main.scss";
import * as calculadora from "./assets/js/calculadora";
// require("./index.pug");

const toggleIcon = document.getElementById("toggle-icon");
const overlayDiv = document.getElementById("overlay");
const nav = document.getElementById("menu");
const body = document.getElementsByTagName("body")[0];
const textoNoticia = document.querySelector("#texto-container");
const overlayNoticia = document.querySelector(".overlay-texto");
const btnShowMore = document.getElementById("btn-leia-mais");
const dropLink = document.querySelector("#informes-nav");
const dropLinkFerramenta = document.querySelector("#ferramentas-nav");
const variavel = document.getElementById("no-exist");
const telefone = document.getElementById("telefone");

calculadora.formCalculadora.addEventListener("submit", (e) => {
  calculadora.calcular();

  e.preventDefault();
});

if (document.querySelectorAll(".lista-noticia-compartilhar")) {
  const compartilhar = document.querySelectorAll(".lista-noticia-compartilhar");

  compartilhar.forEach((element) => {
    element.addEventListener("click", (e) => {
      element.classList.toggle("active");
    });
  });
}

if (document.getElementById("btn-leia-mais")) {
  const btnShowMore = document.getElementById("btn-leia-mais");

  btnShowMore.addEventListener("click", showMore);

  if (textoNoticia.innerText.length < 426 || screen.width >= 1024) {
    textoNoticia.style.height = "auto";
    overlayNoticia.style.display = "none";
    btnShowMore.style.display = "none";
  }

  function showMore() {
    textoNoticia.style.height = "auto";
    overlayNoticia.style.display = "none";
    btnShowMore.style.display = "none";
  }
}

dropLink.addEventListener("click", (e) => {
  if (dropLink.classList.contains("active")) {
    dropLink.classList.remove("active");
    dropLink.setAttribute("aria-expanded", "false");
    dropLink.setAttribute("aria-label", "Abrir submenu");
    dropLink.nextElementSibling.style.marginTop = "0rem";
    overlayDiv.style.visibility = "hidden";
  } else {
    dropLink.classList.add("active");
    dropLink.setAttribute("aria-expanded", "true");
    dropLink.setAttribute("aria-label", "Fechar submenu");
    overlayDiv.style.visibility = "visible";

    if (screen.width < 1279) {
      dropLink.nextElementSibling.style.marginTop = "8.25rem";
    }
  }
});

dropLinkFerramenta.addEventListener("click", (e) => {
  if (dropLinkFerramenta.classList.contains("active-ferramentas")) {
    dropLinkFerramenta.classList.remove("active-ferramentas");
    dropLinkFerramenta.nextElementSibling.style.marginTop = "0rem";
    dropLinkFerramenta.setAttribute("aria-expanded", "false");
    dropLinkFerramenta.setAttribute("aria-label", "Abrir submenu");
    // OVERLAY
    overlayDiv.style.visibility = "hidden";
  } else {
    dropLinkFerramenta.setAttribute("aria-expanded", "true");
    dropLinkFerramenta.setAttribute("aria-label", "Fechar submenu");
    dropLinkFerramenta.classList.add("active-ferramentas");
    // OVERLAY
    overlayDiv.style.visibility = "visible";

    if (screen.width < 1279) {
      dropLinkFerramenta.nextElementSibling.style.marginTop = "26.5rem";
    }
  }
});

function closeMenu() {
  nav.classList.remove("active");
  overlayDiv.style.visibility = "hidden";
  body.style.overflow = "visible";
  toggleIcon.setAttribute("aria-expanded", "false");
  toggleIcon.setAttribute("aria-label", "Abrir menu");
}

toggleIcon.addEventListener("click", function (e) {
  if (nav.classList.contains("active")) {
    closeMenu();
  } else {
    toggleIcon.setAttribute("aria-expanded", "true");
    toggleIcon.setAttribute("aria-label", "Fechar menu");
    overlayDiv.style.visibility = "visible";
    nav.classList.add("active");
    body.style.overflow = "hidden";
    e.currentTarget.setAttribute("aria-expanded", "true");
    toggleIcon.setAttribute("aria-label", "Fechar menu");
  }
});

overlayDiv.addEventListener("click", function (e) {
  closeMenu();

  dropLink.classList.remove("active");
  dropLink.setAttribute("aria-expanded", "false");
  dropLink.setAttribute("aria-label", "Abrir submenu");
  dropLink.nextElementSibling.style.marginTop = "0rem";

  dropLinkFerramenta.classList.remove("active-ferramentas");
  dropLinkFerramenta.nextElementSibling.style.marginTop = "0rem";
  dropLinkFerramenta.setAttribute("aria-expanded", "false");
  dropLinkFerramenta.setAttribute("aria-label", "Abrir submenu");
});
