export function showpay(rentabilidadeValue, desconto) {
  if (tempoAnos.value < 0) {
    tempoAnos.value = 0;
  }
  var pv = parseFloat(capitalAcumulado.value);
  var meses = parseFloat(tempoAnos.value * 12);
  var rentabilidade = parseFloat(rentabilidadeValue / 100);
  var rentabilidade_mensal = parseFloat(Math.pow(1 + rentabilidade, 1 / 12) - 1);
  var aporte = parseFloat(aporteMensal.value);
  var juros = parseFloat(Math.pow(1 + rentabilidade_mensal, meses));

  if (desconto) {
    return (((Math.round(
      (pv * juros + (aporte * (juros - 1)) / rentabilidade_mensal) * 100
    ) / 100 ) * 0.85).toFixed(0)).toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ".");
  }

  return (
    (Math.round(
      (pv * juros + (aporte * (juros - 1)) / rentabilidade_mensal) * 100
    ) / 100).toFixed(0)
  ).toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ".");
}
